.App {
  text-align: center;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  font-size: calc(10px + 2vmin);
  color: white;
  background-image: url("assets/background.png");
  background-size: cover;
}


.app-nav{
  background-color: #fca311;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  min-width: 100%;
  min-height: 5vh;
  text-align: center;
  padding-top: 3vh;
  padding-bottom: 3vh;
}

.app-nav h3 {
  color: white;
  margin: 0;
}

.nav-links-container {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}

.nav-link {
  color: #fefefe;
  margin-right: 3vh;
  text-decoration: none;
}

.nav-link:hover {
  color: #3C153B;
  margin-right: 3vh;
  text-decoration: none;
}

.about-section-container {
  background-color: #ffffff;
  min-width: 40%;
  margin-top: 10%;
  margin-left: 22%;
  border-bottom-left-radius: 30px;
  box-shadow: inset 0px 0px 5px 0px black;
  padding: 5vh;
  padding-top: 10vh;
  color: #000000;
  text-align: left;
}

.post-list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.post-list-link {
  text-decoration: none;
  color: #fefefe;
}

.post-list-link:hover {
  color: #c3eeea;
}

.post-container {
  background-color: white;
  margin: 50px;
  color: #282c34;
  text-align: start;
  font-size: 20px;
  padding: 20px;
  border-radius: 20px;
}


.post-list-container {
  margin-top: 5%;
}
